import { store } from "react-notifications-component";
import { baseURL, header, routes, cruspoFetch} from "./config";
export async function raiseTicket(data){
    const response = await cruspoFetch(baseURL.adminBaseURL+routes.raiseTicket,{
        body:JSON.stringify(data),
        method:'POST',
        headers:header,
        mode:'cors'
    });
    if(response.ok){
        store.addNotification({
            title: "Ticket raised succesfully",
            message: "our executive will contact you shortly",
            type: "success",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return true;
    }else{
        store.addNotification({
            title: "something went wrong",
            message: "error ",
            type: "danger",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return false;
    }
}