import { store } from "react-notifications-component";
import { baseURL, header, routes, cruspoFetch } from "./config";

export async function createSession(data){
    const response = await cruspoFetch(baseURL.adminBaseURL+routes.addSession,{
        body:JSON.stringify(data),
        headers:header,
        mode:'cors',
        method:'POST'
    });
    if(response.ok){
        store.addNotification({
            title: "Session added Successfully",
            message: "Session added Successfully",
            type: "success",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return true;
    }else{
        store.addNotification({
            title: "Error while adding session",
            message: "can't add Session",
            type: "danger",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return false;
    }
}

