export async function getSessions(type){

    var url;
    if(type==='upcoming')
    {
        url = baseURL.adminBaseURL+routes.getUpcomingSessions; 
    }else if(type==='past'){
        url = baseURL.adminBaseURL+routes.getPastSessions; 
    }
    const response = await cruspoFetch(url,{
        method:'GET',
        mode:'cors',
        headers:header
    });
    if(response.ok){
       return await response.json();
    }else{
        store.addNotification({
            title: `Error cruspoFetching ${type} Sessions`,
            message: "can't cruspoFetch sessions",
            type: "success",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
    }
}