import { useState } from "react";
import Lesson from './lesson';

const Module = (props) => {
  
    const [lessons, setLessons] = useState([]);
    
    const addLesson = ()=>{
      setLessons((lessons)=>[...lessons,lessons.length+1]);
    }
  
    return (
      <div className="module-container">
        <div className="module-content">
            <div className="flex" style={{justifyContent:'space-between',alignItems:'center'}}>
                <p className="title1">{props.index+1}. Program content Module</p>
                <div className="remove-module-button" onClick={(()=>props.removeModule(props.index))}>
                    <p>Remove Module</p>
                    <img src="/Images/cancel-button.svg" alt="cancel-button"/>
                </div>
            </div>
            <input className="cruspo-input mt-10" placeholder="Give your module a title"/>
            <textarea className="cruspo-textarea" placeholder="Give your module a description"></textarea>
            {
              lessons.map((l,index)=>{
                return <Lesson lessonIndex={index}/> 
             })
           }
            <div className="flex space-between add-lesson-button" style={{width:'10%',borderRadius:'50px'}} onClick={()=>addLesson()}>
              <p>Add Lesson</p>
              <img src="/Images/add-member.svg" alt="alt"/>
            </div>
        </div>
      </div>
    );
  };

  export default Module;