import React,{ Component, useEffect, useState } from "react";
import AppointmentCard from "./appointment-card";
import Tabs from "../Helpers/Tab";
import ReactModal from "react-modal";
import currencies from '../../../helper/currencies';
import Loader  from 'react-loader-spinner';
import {createAppointment,getAppointments,getBookings} from '../../../services/appointmentService';
import moment from "moment";
import { uploadFileandGetLink } from "../../../services/resourceServices";

export default class Appointment extends Component{
    
    constructor(props){
        super(props);
        this.state = {
            loading:false,
            isCreateAppointmentModalOpen:false
        };
        this.fetchAppointments();
    }

    fetchAppointments = async ()=>{
        var appointments = await getAppointments();
        this.setState({appointments:appointments,loading:true});
    }

    closeCreateAppointmentModal = ()=>{
        this.setState({
            isCreateAppointmentModalOpen:false
        })
    }
    
    openCreateAppointmentModal = ()=>{
        this.setState({
            isCreateAppointmentModalOpen:true
        })
    }
    
    render(){
        return (
        <>
            <CreateAppointmentModal
                isCreateAppointmentModalOpen={this.state.isCreateAppointmentModalOpen}
                closeCreateAppointmentModal={this.closeCreateAppointmentModal}
            />
            <Tabs>
                <div label="Appointments">
                    {this.state.loading?
                    <div className="appointment-container">
                            <>
                                { this.state.appointments.map((appointment)=>{
                                    return <AppointmentCard details={appointment}/>
                                })}
                                    <button onClick={this.openCreateAppointmentModal} className="cruspo-hover-bottom-right-button space-evenly">
                                        <p className="title1">Appointment</p>
                                        <img src="/Images/add-member.svg" style={{width:'25px',height:'25px'}} alt="add-program"/>
                                    </button>
                            </>
                            </div>
                        :<div className="loading-center">
                            <Loader
                            type="TailSpin"
                            color="#ff9f1c"
                            height="50px"
                            width="50px"
                            />
                        </div>}
                </div>
               
                <div label="Upcoming appointments">
                    <UpcomingPastAppointment type="upcoming"/>
                </div> 
                <div label="Past appointments">
                    <UpcomingPastAppointment type="past"/>
                </div>
                
            </Tabs>
        </>
        );
    }
}


const UpcomingPastAppointment = (props)=>{
    
    const [upcomingPastAppointmentState, setUpcomingPastAppointmentState] = useState({
        isLoading:true,
        bookings:[]
    });

    useEffect(() => {
        bookings();
        setUpcomingPastAppointmentState({
            isLoading:true
        });
    }, []);
    
    async function bookings(){
        var bookings = await getBookings(props.type);
        console.log(bookings);
        if(bookings){
            setUpcomingPastAppointmentState({
                isLoading:false,
                bookings:bookings
            });
        }else{

        }
    }

    return (
        <>
        {
            upcomingPastAppointmentState.isLoading?
                <div className="loading-center">
                    <Loader
                    type="TailSpin"
                    color="#ff9f1c"
                    height="50px"
                    width="50px"
                    />
                </div>
            :<div className="upcoming-appointment">
                {   
                    upcomingPastAppointmentState.bookings.length===0?
                    <div className="flex" style={{alignItems:'center',justifyContent:'center'}}>
                        <h1>No appointments Currently</h1>
                    </div>
                    :upcomingPastAppointmentState.bookings?.map(b=>{
                        return <BookedAppoitment details={b}/>
                    }) 
                }
            </div>
        }
        </>
    );

}

const CreateAppointmentModal = (props)=>{
    
    const [appointmentTitle, setAppointmentTitle] = useState();
    const [appointmentDescription, setAppointmentDescription] = useState();
    const [appointmentIsPaid, setAppointmentIsPaid] = useState();
    const [appointmentCurrency, setAppointmentCurrency] = useState();
    const [appointmentPrice, setappointmentPrice] = useState();
    const [appointmentThumbnail, setappointmentThumbnail] = useState();
    const openFile = React.useRef(null);
    
    const handleChange = (e)=>{
        setappointmentThumbnail(e.currentTarget.files[0])
    }
    
    const handleClick = (e)=>{
        openFile.current.click()
    }

    const handleSubmit = async (e)=>{
        e.preventDefault();
        const res = await createAppointment({
            'appointmentTitle':appointmentTitle,
            'appointmentDescription':appointmentDescription,
            'appointmentThumbnail':await uploadFileandGetLink(appointmentThumbnail),
            'isPaid':appointmentIsPaid,
            'appointmentPrice':appointmentPrice,
            'currency':appointmentCurrency,
        });
        if(res){
            props.closeCreateAppointmentModal();
        }else{
            
        }
    }
    return (
        <ReactModal
                isOpen={props.isCreateAppointmentModalOpen}
                shouldCloseOnEsc={true}
                shouldCloseOnOverlayClick={true}
                onRequestClose={props.closeCreateAppointmentModal}
                className="create-appointment-modal"
                overlayClassName="modal-overlay"
            >
                <div className="modal-close-row">
                    <p >Create Appointment</p>
                    <button class="modal-close-button" onClick={props.closeCreateAppointmentModal}>Close <img src="/Images/cancel-button.svg" alt="cancel-icon"/></button>
                </div>
                <div className="modal-content">
                    <form onSubmit={(e)=>handleSubmit(e)}>
                        <div className="flex space-evenly">
                     
                            <div className="program-add-image-wrapper" onClick={handleClick}>
                                <input type="file" ref={openFile} onChange={handleChange} style={{display:'none'}}/>
                                {
                                    !appointmentThumbnail?
                                    <div className="flex" style={{flexDirection:'column',alignItems:'center'}}>
                                        <img src="/Images/upload-thumbnail.svg" alt="upload-thumbnail"/>
                                        <p>Upload your Image</p>
                                    </div>
                                    : <div className="appointment-thumbnail">
                                        <img src={URL.createObjectURL(appointmentThumbnail)} style={{width:'300px',height:'200px'}} alt="appointment-thumbnail"/>
                                    </div>
                                }
                            </div>
                            <img src="/Images/cruspo-extra.svg" alt="imagecontainer"/>
                        </div>
                        <input className="cruspo-input mt-10" placeholder="Appointment title" onChange ={(e)=>setAppointmentTitle(e.currentTarget.value)}/>
                        <textarea className="cruspo-textarea" placeholder="Appointment description" onChange={(e)=> setAppointmentDescription(e.currentTarget.value)}></textarea>
                        <div className="flex space-between mt-20">
                            <div>
                                <p className="title1">Make this a paid Appointment</p>
                                <p className="description1">
                                    This will require your audience to pay for your appointment
                                </p>
                            </div>
                            <label className="cruspo-switch">
                            <input
                                type="checkbox"
                                checked={appointmentIsPaid}
                                onClick={(e)=>setAppointmentIsPaid(e.currentTarget.checked)}
                            />
                            <span className="slider"></span>
                            </label>
                        </div>
                        {
                            appointmentIsPaid?
                            <div className="flex space-evenly mt-20">
                                <select className="cruspo-select mr-20" onChange={(e)=>setAppointmentCurrency(e.target.value)}>
                                    <option>Select Currency</option>
                                    {
                                        currencies.map(c=>{
                                            return <option value={c._id.$oid}>{`${c.fullName} (${c.symbol})`}</option>
                                        })
                                    }
                                </select>
                                <input className="cruspo-input" placeholder="appointment price" onChange={(e)=>setappointmentPrice(e.target.value)}/>
                            </div>:<div></div>
                        }
                        <button type="submit" className="cruspo-button create-appointment-button">Create Appointment</button>
                    </form>
                </div>
            </ReactModal>
    )
}

