import {Component} from 'react';
import { Switch,Route } from 'react-router-dom';
import HelpAndSupport from './HelpAndSupport/helpAndSupport';
import Account from './Account/account';
import JoinLive from './Go live/joinlive';
import Program from './Programs/program';
import Dashboard from './Dashboard/dashboard';

class CruspoUserContainer extends Component{
    render(){
        return (
            <div className="cruspo-user-container">
            {/* <Header/> */}
            <div style={{marginTop:'60px'}}>
                <Switch>
                    <Route exact path="/user">
                        <Dashboard />
                    </Route>
                    <Route exact path="/user/dashboard">
                        <Dashboard />
                    </Route>
                    <Route exact path="/user/programs">
                        <Program />
                    </Route>
                    <Route exact path="/user/joinlive">
                        <JoinLive />
                    </Route>
                    <Route exact path="/user/account">
                        <Account/>
                    </Route>
                    <Route exact path="/user/help-and-support">
                        <HelpAndSupport/>
                    </Route>
                </Switch>
          </div>
          </div>

        );
    }
}

export default CruspoUserContainer;