import {Component} from 'react';
import '../../../Assets/css//admin/bookAppointment.css';
import Calendar from 'react-calendar';
import Modal from 'react-modal';
import { store } from 'react-notifications-component';
import { getAppointmentById } from '../../../services/appointmentService';
import Loader from 'react-loader-spinner';
import moment from 'moment';
import CruspoTimeCalculator from '../../../helper/CruspoTimeCalculator';

class AppointmentInfo extends Component{
    
    constructor(props){
        super(props);
        this.state = {
            activeStartDate:new Date(Date.now()),
            selectedDate : new Date(Date.now()),
            isModalOpen:false,
            isLoading:true,
        }
    }
        
    async componentDidMount(){
        var appointmentData = await getAppointmentById(window.location.href.split('/').pop());
        if(appointmentData.ok)
        {   var overridenDates = [];
            var data = await appointmentData.json();
            data.dateOverride.forEach(element => {
                overridenDates.push.apply(overridenDates,element.dates);
            });
            var newOrverridenDate = overridenDates.map(d=>{
                return moment(d).format('YYYY-MM-DD');
            });
            this.setState({isLoading:false,appointmentData:data,overridenDates:newOrverridenDate});
        }
    }

    getAvailableTimes = (selectedDate)=>{
        const d = moment(selectedDate).format('YYYY-MM-DD');
        let ctc;
        if(this.state.overridenDates.includes(d)){
            console.log(true);
        }else{
            var day = moment(selectedDate).format('ddd');
            this.state.appointmentData.schedule[day].forEach(d=>{
                ctc = new CruspoTimeCalculator(9,0,'12-hour');
            });
        }
        console.log(ctc.getTimeListBasedOnDuration(this.state.appointmentData.duration.minutes));
        return ctc.getTimeListBasedOnDuration(this.state.appointmentData.duration.minutes);
    }

    onSelectDate = (day)=>{
        this.setState({selectedDate:day})
        this.openModal();
        // console.log(typeof day);
    }

    closeModal = ()=>{
        this.setState({isModalOpen:false,timeSelected:undefined})
    }

    openModal = ()=>{
        this.setState({isModalOpen:true});
    }
    
    selectTime = (time)=>{
        this.setState({timeSelected:time});
        console.log(time)
    }

    bookAppointment = async ()=>{
        const response  = await fetch('http://localhost:5000/bookappointment/book',{
            method:'POST',
            body:JSON.stringify({
                email:this.email,
                name:this.name,
                bookedDate:this.state.selectedDate,
                bookedTime:this.state.timeSelected
            }),
            headers:{
                'Content-type':'application/json'
            }
        });
        if(response.ok){
            this.setState({appointmentBooked:true});
        }else{
            store.addNotification({
                    title: "Oops...",
                    message: "Something went wrong please try again later",
                    type: "danger",
                    insert: "top",
                    container: "bottom-right",
                    animationIn: ["animate__flipInX"],
                    // animationOut: ["animate__fadeOut"],
                    dismiss: {
                        duration: 2000,
                        //   onScreen: true
                        }
                    });
        }
    }

    render(){
        return (
            !this.state.isLoading?<div className="bookappointment-wrapper">
                <div className="bookappointment-details">
                    <img  className="bookappointment-thumbnail" src={this.state.appointmentData.appointmentThumbnail??'/Images/bookappointment.jpeg'} alt="appointment Thumbnail"/>
                    <div className="appointmentDetails">
                            <p className="appointment-title">{this.state.appointmentData.appointmentTitle}</p>
                            <p className="appointment-description">{this.state.appointmentData.appointmentDescription}</p>
                            <div className="price-duration-wrapper">
                                <div className="appointment-price">
                                    <img src="/Images/duration-icon.svg" alt="watch-icon"/>
                                    <p>{`${this.state.appointmentData.duration.hours} hours ${this.state.appointmentData.duration.minutes} mins`}</p>
                                </div>
                                <div className="appointment-duration">
                                    <img src="/Images/earning-history-on-click.svg" alt="wallet-Icon"/>
                                    <p>{this.state.appointmentData.appointmentPrice===0?'Free':`${this.state.appointmentData.appointmentPrice} ${this.state.appointmentData.currency.symbol}`}</p>
                                </div>
                            </div>
                    </div>
                    <Modal
                    isOpen={this.state.isModalOpen}
                    // style={customStyles} 
                    className="bookappointment-modal"
                    ariaHideApp={false}
                    overlayClassName="bookappointment-modal-overlay"
                    onRequestClose={this.closeModal}
                    contentLabel="Select your Suitable time"
                    >   
                    {
                        !this.state.appointmentBooked?<>
                        <div className ="modal-close">
                            <div>
                                {this.state.timeSelected!=null?<img src="/Images/arrow-black.svg" alt="arrow_black" onClick={()=>this.selectTime()} className="backArrow"/>:<h2 style={{display:'inline'}}>Select your suitable time</h2>}
                            </div>
                            <img src="/Images/cancel-button.svg" onClick={()=>{this.closeModal()}} alt="close_icon"/>
                        </div>
                        <div className="time-wrapper">
                            <div className="time-image-wrapper">
                                    <img src="/Images/appointment-details.svg" alt="appointment_details"/>
                            </div> 
                            {this.state.timeSelected==null?<div className="time-box-wrapper">
                                {
                                    this.getAvailableTimes(this.state.selectedDate).map(t=>{
                                        return <div className="time-box" onClick={()=>this.selectTime(t)}>{t}</div>
                                    })
                                }
                            </div>:
                            <div className="bookappointment-form">
                                <p>You are booking appointment for {this.state.selectedDate.toDateString()} at {this.state.timeSelected}(Indian sandard time (IST))</p>
                                <input type="text" onChange={(e)=>{this.name=e.target.value}} placeholder="Full name"/>
                                <input type="text" onChange={(e)=>{this.email=e.target.value}} placeholder="best email address"/>
                                <div className="confirm" onClick={()=>this.bookAppointment()}>Continue</div>
                            </div>
                            }
                            </div>
                            </>
                        :<div className="appointmentBooked-success">
                            <img src="/Images/confirm.svg" alt="asdvafb"/>
                            <p> Your appointment has been booked for cruspo 1:1 demo on {this.state.selectedDate.toDateString()} at {this.state.timeSelected}</p>
                            <button > <a href="https://cruspo.com"> Return to site </a></button>
                        </div>
                    }
                </Modal>
                </div>
                <Calendar 
                        minDate={this.state.activeStartDate}
                        onChange={this.onSelectDate}
                        // tileContent="/nsaflknhabf"
                        // defaultActiveStartDate={new Date(Date.now())}
                        // showNavigation ={true}
                        // tileContent={(props)=>true}
                        tileDisabled ={(p)=>this.state.overridenDates.includes(p.date)}
                        
                        // defaultValue={new Date(Date.now())}
                        />
                
            </div>:<div className="loading-center">
                            <Loader
                            type="TailSpin"
                            color="#ff9f1c"
                            height="50px"
                            width="50px"
                            />
                        </div>
        );
    }
}

export default AppointmentInfo;