import {Component} from 'react';
import { Switch,Route } from 'react-router-dom';

import Website from './Website/website';
import Settings from './Setting/settings';
import HelpAndSupport from './Help-and-support/help-and-support';
import Earning from './Earning/earning';
import Programs from './Programs/programs';
import Appointment from './Appointment/Appointment';
import EditAppointment from './Appointment/edit-appointment';
import Dashboard from './Dashboard/Dashboard';
import Resource from './Resource/Resource';
import Blog from './Blog/Blog';
import Golive from './GoLive/golive';
import ManageMembers from './ManageMembers/manageMembers';
import Header from './Header/header';
import Editprograms from './Programs/edit-programs';

class CruspoContainer extends Component{
    render(){
        return (
            <div className="cruspo-admin-container">
            <Header/>
            <div style={{marginTop:'60px'}}>
                <Switch>
                    <Route exact path="/admin/">
                        <Dashboard />
                    </Route>
                    <Route exact path="/admin/website">
                        <Website />
                    </Route>
                    <Route path="/admin/programs" render={
                        ({match:{url}})=>(
                            <>
                                <Route path={`${url}/`} component={Programs} exact/>
                                <Route path={`${url}/edit/live/:programId`} component={Editprograms} />
                                <Route path={`${url}/edit:programId`} exact/>
                            </>
                        )
                    } />
                    <Route path="/admin/appointment" 
                        render={
                            ({match:{url}})=>(
                                <>
                                    <Route path={`${url}/`} component={Appointment} exact/>
                                    <Route path={`${url}/edit/:appointmentId`} component={EditAppointment} />
                                    {/* <Route path={`${url}/edit:appointmentId`} exact/> */}
                                </>
                            )
                        }
                    />
                    <Route exact path="/admin/resources">
                        <Resource />
                    </Route>
                    <Route exact path="/admin/go-live">
                        <Golive />
                    </Route>
                    <Route exact path="/admin/blogs">
                        <Blog/>
                    </Route>
                    <Route exact path="/admin/manage-member">
                        <ManageMembers />
                    </Route>
                    <Route exact path="/admin/marketing">
                        <p>Hello marketing</p>
                    </Route>
                    <Route exact path="/admin/earning">
                        <Earning />
                    </Route>
                    <Route exact path="/admin/settings">
                        <Settings />
                    </Route>
                    <Route exact path="/admin/help-and-support">
                        <HelpAndSupport />
                    </Route>
                </Switch>
          </div>
          </div>

        );
    }
}

export default CruspoContainer;