import React, { Component } from 'react'
import Tabs from '../../Admin/Helpers/Tab';
import * as authenticationServices from '../../../services/authenticationServices';
import { store } from 'react-notifications-component';

export default class Login extends Component {
    login = async (e)=>{
        e.preventDefault();
        const response = await authenticationServices.userLogin({
            email:this.state.email,
            password:this.state.password,
            domainName:window.location.hostname
        });
        if(response){
            this.props.history.push('/user/dashboard');
        }else{

        }
    }
    signup = async (e)=>{
        e.preventDefault();
        if(this.state.password!==this.state.confirmPassword){
            this.setState({error:'Password and confirm password doesn\'t match'});
            return ;
        }
        const response = await authenticationServices.userSignup({
            firstName:this.state.firstName,
            lastName:this.state.lastName,
            email:this.state.email,
            password:this.state.password,
            domainName:window.location.hostname
        });
        if(response){
            window.location.reload();
            // this.props.history.push('/login');
        }
    }
    render() {
        return (
            <div className="login-wrapper">
                <div className="user-login-container">
                        <div style={{display:'flex',alignItems:'center'}}>
                            <img src="/Images/log-in.svg" alt="login_form" style={{flexBasis:'40%',maxWidth:'500px'}}/>
                        </div>
                        <div style={{flexBasis:'80%'}} >
                            <Tabs>
                                <div label="Log In">
                                    <form onSubmit={(e)=>this.login(e)} style={{width:'100%',padding:'0 50px'}} className="mt-20">
                                        <input type="text" name="email" placeholder="Registered email"  className="cruspo-input mt-10" style={{ width:'80%'}} onChange={(e)=>{this.setState({email:e.currentTarget.value})}} />
                                        <input type="password" placeholder="password"  className="cruspo-input mt-10" style={{ width:'80%'}} onChange={(e)=>{this.setState({password:e.currentTarget.value})}} />
                                        <button type="submit" className="login-button mt-10">Login</button>
                                    </form>
                                    <p className="mt-20" style={{paddingLeft:'50px',fontSize:'20px',color:'#1462e1'}}>Forget password</p>
                                    <p className="mt-10" style={{paddingLeft:'50px',fontSize:'20px'}}>Not a registered user ?<span style={{color:'#1462e1'}}> Register</span></p>
                                </div>
                                <div label="Sign up">
                                    <form onSubmit={(e)=>this.signup(e)} style={{width:'80%',padding:'0 50px'}}>
                                        <div className="flex space-between">
                                            <input type="text" name="firstName" placeholder="First name"  className="cruspo-input mt-10" style={{ width:'48%'}} onChange={(e)=>{this.setState({firstName:e.currentTarget.value})}}/>
                                            <input type="text" name="lastName" placeholder="Last name"  className="cruspo-input mt-10" style={{ width:'50%'}} onChange={(e)=>{this.setState({lastName:e.currentTarget.value})}}/>
                                        </div>
                                        <input type="email" name="email" placeholder="Email"  className="cruspo-input mt-10" style={{ width:'100%'}} onChange={(e)=>{this.setState({email:e.currentTarget.value})}}/>
                                        <input type="password" placeholder="password"  className="cruspo-input mt-10" style={{ width:'100%'}} onChange={(e)=>{this.setState({password:e.currentTarget.value})}}/>
                                        <input type="password" placeholder="confirm password"  className="cruspo-input mt-10" style={{ width:'100%'}} onChange={(e)=>{this.setState({confirmPassword:e.currentTarget.value})}}/>
                                        <button type="submit" className="signup-button mt-20">Sign up</button>
                                    </form>
                                </div>
                            </Tabs>
                        </div>
                </div>
            </div>

        )
    }
}
