import React, { Component,useEffect,useState } from 'react';
import ReactModal from 'react-modal';
import '../../../Assets/css/admin/managemembers.css';
import countryCode from '../../../helper/CountryCodes';
import { store } from 'react-notifications-component';
import * as manageMemberServices from '../../../services/managememberServices';

export default class ManageMembers extends Component {
    constructor(props){
        super(props);
        this.state = {
            isCreateGroupModalOpen:false,
            isAddMemberModalOpen:false,
            members:[]
        }
    }
    
    closeModal = ()=>{
        this.setState({
            isCreateGroupModalOpen:false
        });
    }

    openCreateGroupModal = ()=>{
        this.setState({
            isCreateGroupModalOpen:true
        });
    }
    
    openAddMemberModal = ()=>{
        this.setState({
            isAddMemberModalOpen:true
        });
    }
    closeAddMemberModal = ()=>{
        this.setState({
            isAddMemberModalOpen:false
        })
    }

    async componentDidMount(){
            var members = await manageMemberServices.getMembers();
            this.setState({members:members})
    }

    render() {
        return (
            <>
            
            <CreateGroupModal 
                isCreateGroupModalOpen={this.state.isCreateGroupModalOpen}
                closeModal={this.closeModal}
                />
            <AddMemberModal 
                isAddMemberModalOpen={this.state.isAddMemberModalOpen}
                closeAddMemberModal={this.closeAddMemberModal}
                />
            <div className="manage-member-wrapper">
                <div className="manage-member-row">
                    <div>
                        <h1>manage all your members at one destination</h1>
                        <div className="flex" style={{marginLeft:'20px'}}>
                            <div onClick={this.openCreateGroupModal} className="managemember-button">
                                <p>Create Group</p>
                                <img src="/Images/add-member.svg" alt="add_icon"/>
                            </div>
                            <div onClick={this.openAddMemberModal} className="managemember-button">
                                <p>Add member</p>
                                <img src="/Images/add-member.svg" alt="add_icon"/>
                            </div>
                        </div>
                    </div>
                    <img src="/Images/Group-56.svg" alt="group" className="manage-member-icon"/>
                </div>
                <div className="search-filter-container">
                    <div className="filter-container">
                        <p>Filter</p>
                        <img src="/Images/filter-icon.svg" alt="filter_icon"/>
                    </div>
                    <div className="searchBar">
                        <input type="text" placeholder="Search members here" />
                        <button>Search</button>
                    </div>
                </div>
                <div className="searchresult-wrapper">
                    <table>
                        <thead>
                            <tr>
                                <th>Sr.</th>
                                <th>Member Name</th>
                                <th>Email</th>
                                <th>contact number</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.members.map((m,index)=>{
                                    return <tr key={index}>
                                        <td>{index+1}.</td>
                                        <td>{m.firstName} {m.lastName}</td>
                                        <td>{m.email}</td>
                                        <td>{m.contactNumber}</td>
                                    </tr>
                                })
                            }
                        
                        </tbody>
                    </table>
                </div>
            </div>
            </>
        )
    }
}

const CreateGroupModal = (props)=>{

    const [groupName, setGroupName] = useState();
    const [isGroupNameAdded, setisGroupNameAdded] = useState(false);
    const [groups, setGroups] = useState([]);

    const handleSubmit = async (e)=>{
        var response = await manageMemberServices.createGroup({
            groupName:groupName,

        });

        if(response)
        {
            props.closeModal();
        }else{

        }
    }

    async function getGroups(){
        var groups = await manageMemberServices.getGroups();
        setGroups(groups);
    }

    useEffect(() => {
        getGroups();
        }, []);

    function updateState(e){
        if(isGroupNameAdded||groups.length==0){
            handleSubmit(e);
        }else{
            setisGroupNameAdded(true);
        }
    }    
    return (
        <ReactModal
                isOpen={props.isCreateGroupModalOpen}
                shouldCloseOnEsc={true}
                shouldCloseOnOverlayClick={true}
                onRequestClose = {props.closeModal}
                overlayClassName="modal-overlay"
                className="createmember-modal"
            >
                <p>Create New Group</p>
                <form onSubmit={(e)=>handleSubmit(e)} style={{width:'100%',display:'flex',flexDirection:'column',alignContent:'space-evenly'}} >
                    {
                    !isGroupNameAdded? 
                    <input className="cruspo-input" placeholder="Group Name" style={{margin:'20px 0px',width:'100%'}} onChange={(e)=>{setGroupName(e.target.value)}}/>
                    :<div>
                        <p className="title2">Add member from Existing group to this group</p>
                        <select className="cruspo-select" style={{width:'100%'}}>
                            <option>Select group</option>
                            {
                                groups.map(g=>{
                                    return <option value={g._id}>{g.groupName}</option> 
                                })
                            }
                        </select>
                    </div>
                    }
                    <div className="flex space-evenly" style={{margin:'20px 0px',width:'100%'}}>
                        <button type="button" onClick={props.closeModal} className="cruspo-button" style={{flexBasis:'40%'}}>Cancel</button>
                        <button type="button" onClick={(e)=>updateState(e)} className="cruspo-button creategroup-button" style={{flexBasis:'40%'}}>{isGroupNameAdded?'Create':'Next'}</button>
                    </div>
                </form>
            </ReactModal>
    );

}

const AddMemberModal = (props)=>{

    const [memberName, setMemberName] = useState();
    const [memberEmail, setMemberEmail] = useState();
    const [memberCountryCode, setCountryCode] = useState();
    const [mobileNumber, setMobileNumber] = useState();
    const [groups, setGroups] = useState([]);
    const [selectedGroup, setSelectedGroup] = useState();
    const [password, setpassword] = useState();

    const getGroups = async ()=>{
        const groups = await manageMemberServices.getGroups();
        setGroups(groups);
    }

    useEffect(() => {
        getGroups();
    }, []);

    const handleSubmit = async (e)=>{ 
        e.preventDefault();
        var response = await manageMemberServices.createMember({
            fullName:memberName,
            email:memberEmail,
            contactNumber:{
                countryCode:memberCountryCode,
                mobileNumber:mobileNumber
            },
            password:password,
            addToGroup:selectedGroup
        });
        if(response.ok){
            const res = await response.json();
            if(res.message==='ALREADY_EXISTS'){
                store.addNotification({
                    title: "Member with this email already Exits",
                    message: "can\'t create new member",
                    type: "danger",
                    insert: "bottom",
                    container: "bottom-right",
                    animationIn: ["animate__animated", "animate__fadeIn"],
                    animationOut: ["animate__animated", "animate__fadeOut"],
                    dismiss: {
                        duration: 5000,
                    }
                });
            }if(res.message==='USER_CREATED'){
                store.addNotification({
                    title: "Member created",
                    message: "new member added successfully",
                    type: "success",
                    insert: "bottom",
                    container: "bottom-right",
                    animationIn: ["animate__animated", "animate__fadeIn"],
                    animationOut: ["animate__animated", "animate__fadeOut"],
                    dismiss: {
                        duration: 5000,
                    }
                });
                props.closeAddMemberModal();
            }
        }else{
            store.addNotification({
                title: "Some error occured please try again",
                message: "can\'t create new member",
                type: "danger",
                insert: "bottom",
                container: "bottom-right",
                animationIn: ["animate__animated", "animate__fadeIn"],
                animationOut: ["animate__animated", "animate__fadeOut"],
                dismiss: {
                    duration: 5000,
                }
            });
        }
    }

    return(
        <ReactModal
        isOpen={props.isAddMemberModalOpen}
        shouldCloseOnEsc={true}
        shouldCloseOnOverlayClick={true}
        onRequestClose = {props.closeAddMemberModal}
        overlayClassName="modal-overlay"
        className="addmember-modal"
    >
        <p>Add New Member</p>
        <form onSubmit={(e)=>handleSubmit(e)} style={{width:'100%',display:'flex',flexDirection:'column',alignContent:'space-evenly'}} >
            <input type="text" className="cruspo-input" placeholder="Member Name" onChange = {(e)=>{setMemberName(e.currentTarget.value)}}style={{margin:'5px 0'}}/>
            <input type="text" className="cruspo-input" placeholder="Member Email" onChange = {(e)=>{setMemberEmail(e.currentTarget.value)}} style={{margin:'5px 0'}}/>
            <div className="flex" style={{margin:'5px 0'}}>
                <select className="cruspo-select" style={{maxWidth:'20%',marginRight:'20px'}} onChange={(e)=>setCountryCode(e.currentTarget.value)}>
                    <option>select country code</option>
                    {
                        countryCode.map(c=>{
                            if(c.dial_code==='+91')
                            {
                                return <option value={c.name} selected>{`${c.dial_code}`}</option>
                            }
                            return <option value={c.name}>{`${c.dial_code}`}</option>
                        })
                    }
                </select>
                <input type="number" className="cruspo-input" placeholder="Member number" style={{flexBasis:'80%'}} onChange={(e)=>setMobileNumber(e.currentTarget.value)}/>
            </div>
            <input type="password" className="cruspo-input" placeholder="default password" onChange = {(e)=>{setpassword(e.currentTarget.value)}} style={{margin:'5px 0'}}/>
            <select className="cruspo-select" style={{margin:'10px 0'}} onChange={(e)=>setSelectedGroup(e.currentTarget.value)}>
                    <option>Add this to group</option>
                    {
                        groups.map(g=>{
                            return <option value={g._id}>{g.groupName}</option>
                        })
                    }
            </select>
            <div className="flex space-evenly" style={{margin:'20px 0px',width:'100%'}}>
                <button type="button" onClick={props.closeAddMemberModal} className="cruspo-button" style={{flexBasis:'40%'}}>Cancel</button>
                <button type="submit" className="cruspo-button creategroup-button" style={{flexBasis:'40%'}}>Add</button>
            </div>
        </form>
    </ReactModal>
    );
}