import React, { Component, createRef,useState } from 'react'
import Tabs from '../Helpers/Tab';
import '../../../Assets/css/admin/resource.css';
import File from './file';
import InlineFile from './inlinefile';
import Folder from './folder';
import ReactModal from 'react-modal';
import { baseURL, routes } from '../../../services/config';
import * as resourceServices from '../../../services/resourceServices';

export default class Resource extends Component {

    constructor(props){
        super(props);
        this.fileDialogRef = createRef(null);
        this.state = {
            isAddFolderModalOpen:false
        }
    }
    
    openFileDialog = ()=>{
        this.fileDialogRef.current.click();
    }

    
    render() {
        return (
            <div className="resource-wrapper">
                
                <Tabs>
                    <div label="All Files">
                        <AllFiles/>
                    </div>
                    <div label="All Folders">
                        <AllFolder/>
                    </div>
                </Tabs>
            </div>
        )
    }
}

class AllFiles extends Component{
    
    constructor(props)
    {
        super(props);
        this.state = {
            isLoading:false,
            files:[],
            recentFiles:[]
        }
        this.fileDialogRef = createRef();
        this.fetchFiles();
    }

    fetchFiles = async ()=>{
        const data = await resourceServices.fetchFiles();
        this.setState({
            recentFiles:data.recentFiles,
            files:data.files
        })
    }

    openFileDialog = ()=>{
        this.fileDialogRef.current.click();
    }

    render(){
        return (
            <>
            <div className="searchBar">
                            <input type="text" placeholder="Search files here" />
                            <button>Search</button>
                        </div>
                        <div className="recent-file-row">
                            {
                                this.state.recentFiles.map(f=>{
                                    return <File details={f}/>
                                })
                            }
                        </div>
                        <div>
                            {
                                this.state.files.map(f=>{
                                    return <InlineFile details={f}/>
                                })
                            }
                        </div>
                        <button className="cruspo-hover-bottom-right-button upload-button" onClick={this.openFileDialog}>
                            <input type="file" ref={this.fileDialogRef} style={{display:"none"}} onChange={()=>{}}/>
                            <img src="/Images/upload-button.svg" alt="upload_button"/>Add Files
                        </button>
            </>
        );
    }
}

class AllFolder extends Component {
    
    constructor(props){
        super(props)
        this.state={
            isAddFolderModalOpen:false,
            folders:[]
        }
    }

    openFolderModal = ()=>{
        this.setState({
            isAddFolderModalOpen:true
        });
    }

    closeAddFolderModal = ()=>{
        this.setState({
            isAddFolderModalOpen:false
        }); 
    }
    async componentDidMount(){
        var response = await resourceServices.fetchFolders();
        if(response.ok)
        {
            this.setState({
                folders:(await response.json()).data
            })
        }
    }

    render() {
        return (
            <div>
                <CreateFolderModal
                    isAddFolderModalOpen={this.state.isAddFolderModalOpen}
                    closeAddFolderModal={this.closeAddFolderModal}
                />
                <div className="searchBar">
                            <input type="text" placeholder="Search folders here" />
                            <button>Search</button>
                        </div>
                        <div className="folder-wrapper">
                            {
                                this.state.folders.map(f=>{
                                    return <Folder
                                            folderName={f.folderName}
                                        />
                                })
                            }
                        </div>
                        <button className="cruspo-hover-bottom-right-button upload-button" onClick={this.openFolderModal}>
                            <img src="/Images/upload-button.svg" alt="upload_button"/>Add Folder
                        </button> 
            </div>
        )
    }
}

const CreateFolderModal = (props)=>{
    const [folderName, setfolderName] = useState();

    const createFolder = async (e)=>{
        e.preventDefault();
        const response = await resourceServices.createFolder({
            folderName:folderName
        });
        if(response){
            props.closeAddFolderModal();
        }
    }

    return (
        <ReactModal
            isOpen={props.isAddFolderModalOpen}
            onRequestClose={props.closeAddFolderModal}
            overlayClassName="modal-overlay"
            className="create-folder-modal"
            shouldCloseOnEsc="true"
            shouldCloseOnOverlayClick="true"
        >
            <p>Create Folder</p>
            <form onSubmit={(e)=>createFolder(e)} >
                <input type="text" className="cruspo-input" placeholder="Folder name" onChange={(e)=>{setfolderName(e.currentTarget.value)}}/>
                <div className="flex space-evenly">
                    <button type="submit" className="cruspo-button"> cancel</button>
                    <button type="submit" className="cruspo-button create-folder"> create</button>
                </div>
            </form>
        </ReactModal>
    );
}