import { Component } from "react";
import ReactModal from "react-modal";
import moment from 'moment';
import timezone from "../../../../helper/timezone";
import {createBatchTiming} from '../../../../services/programServices';

class AddEditBatchTimingModal extends Component{
    
    constructor(props){
        super(props);
        this.state={}
    }

    handleSubmit = async (e)=>{
        e.preventDefault();
        const res = await createBatchTiming({
            bacthTitle:this.state.bacthTitle,
            startDate:this.state.startDate,
            startTime:this.state.startTime,
            timezone:this.state.timezone,
            duration:{
                hours:this.state.hours,
                mins:this.state.mins
            },
            isNotify:this.state.isNotify,
            limitSeat:this.state.limitSeat,
            noOfSeat:this.state.noOfSeat
        });
    }
    
    onChangeDays = (e)=>{
        if(!this.state.selectedDays.includes(e.target.value) && e.target.checked)
        {
            this.state.selectedDays.push(e.target.value);
        }else{
            this.state.selectedDays.splice(this.state.selectedDays.indexOf(e.target.value), 1)
        }
    }

    render(){
        return(
            <ReactModal
                isOpen={this.props.isOpen}
                onRequestClose={this.props.closeAddBatchTimingModal}
                shouldCloseOnEsc="true"
                shouldCloseOnOverlayClick="true"
                className="add-batch-timing-modal"
                overlayClassName="modal-overlay"
            >
                <div className="modal-close-row">
                    <p>Add Batch Timings</p>
                    <button class="modal-close-button" onClick={this.props.closeAddBatchTimingModal}>Close <img src="/Images/cancel-button.svg" alt="cancel-icon"/></button>
                </div>
            <div className="edit-batch-modal-body">
                <form onSubmit={(e)=>this.handleSubmit(e)}>
                    <input type="text" placeholder="Give a title to your batch timing" className="cruspo-input" onChange={(e)=>{this.setState({bacthTitle:e.currentTarget.value})}}/>
                    <div class="program-inline-wrapper flex mt-20" style={{justifyContent:'space-evenly'}}>
                        <div className="mr-10">
                            <label className="title1" >Start Date</label>
                            <input type="date" className="cruspo-input" min={moment().format('YYYY-MM-DD')} style={{flexBasis:'40%',marginRight:'5%'}} onChange={(e)=>{this.setState({startDate:e.currentTarget.value})}}/>
                        </div>
                        <div className="mr-10">
                            <label className="title1" >start time</label>
                            <input type="time" className="cruspo-input" style={{flexBasis:'30%',marginRight:'5%'}} onChange={(e)=>{this.setState({startTime:e.currentTarget.value})}}/>
                        </div>
                        <div >
                            <label className="title1">Timezone</label>
                            <select className="cruspo-select" style={{width:'80%',margin:'0px 0px'}} onChange={(e)=>{this.setState({timezone:e.currentTarget.value})}}>
                                <option>Select timezone</option>
                                {
                                    timezone.map(t=>{
                                        return <option value={t}>{t}</option>
                                    })
                                }
                            </select>
                        </div>
                    </div>
                    <div class="program-inline-wrapper flex space-between mt-20" style={{alignItems:'center'}} >
                        <p className="title1" style={{flexBasis:'60%'}}>Duration of the session</p>
                        <div style={{flexBasis:'50%',justifyContent:'space-evenly'}} className="flex" >
                            <input type="number" min="0" className="cruspo-input" placeholder="hours" style={{width:'20%',marginRight:'20px',flexBasis:'50%'}} onChange={(e)=>{this.setState({hour:e.currentTarget.value})}}/>
                            <input type="number" min="0" max="60" className="cruspo-input" placeholder="mins" style={{width:'20%',flexBasis:'50%'}} onChange={(e)=>{this.setState({mins:e.currentTarget.value})}}/>
                        </div>
                    </div>
                    <div class="program-inline-wrapper mt-20" style={{margin:'30px 0px'}}>
                        <div className="flex space-between">
                            <div>
                                <p className="title1">Schedule for session </p>
                                <p className="description1">This will repeat your live session based on your choice. Default value is once.</p>
                            </div>
                            <div className="flex">
                                <label class="cruspo-radio-container">Once
                                    <input type="radio"  name="repeatFrequency" selected value="ONCE" onChange={(e)=>{
                                        this.setState({repititionFrequency:e.currentTarget.value})
                                        }}/>
                                    <span class="checkmark"></span>
                                </label>
                                <label class="cruspo-radio-container">Daily
                                    <input type="radio"  name="repeatFrequency" value="DAILY" onChange={(e)=>{
                                        this.setState({repititionFrequency:e.currentTarget.value})
                                        }}/>
                                    <span class="checkmark"></span>
                                </label>
                                <label class="cruspo-radio-container">Mon-Fri
                                    <input type="radio" name="repeatFrequency" value="MONTOFRI" onChange={(e)=>{
                                        this.setState({repititionFrequency:e.currentTarget.value})
                                        }}/>
                                    <span class="checkmark"></span>
                                </label>
                                <label class="cruspo-radio-container">custom
                                    <input type="radio" name="repeatFrequency" value="CUSTOM" onChange={(e)=>{
                                        this.setState({repititionFrequency:e.currentTarget.value,selectedDays:[]})
                                        }}/>
                                    <span class="checkmark"></span>
                                </label>
                            </div>
                        </div>
                        {
                        this.state.repititionFrequency==='CUSTOM'?
                        <div className="flex mt-20" style={{width:'100%',justifyContent:'flex-end'}}>
                        <label className="cruspo-checkbox-container">
                            <input type="checkbox" value="sun"  onChange={(e)=>{this.onChangeDays(e)}}/>
                            <span className="cruspo-checkmark">SUN</span>
                        </label>
                        <label className="cruspo-checkbox-container">
                            <input type="checkbox" value="mon"  onChange={(e)=>{this.onChangeDays(e)}}/>
                            <span className="cruspo-checkmark">MON</span>
                        </label>
                        <label className="cruspo-checkbox-container">
                            <input type="checkbox" value="tue"  onChange={(e)=>{this.onChangeDays(e)}}/>
                            <span className="cruspo-checkmark">TUE</span>
                        </label>
                        <label className="cruspo-checkbox-container">
                            <input type="checkbox" value="wed"  onChange={(e)=>{this.onChangeDays(e)}}/>
                            <span className="cruspo-checkmark">WED</span>
                        </label>
                        <label className="cruspo-checkbox-container">
                            <input type="checkbox" value="thu" onChange={(e)=>{this.onChangeDays(e)}}/>
                            <span className="cruspo-checkmark">THU</span>
                        </label>
                        <label className="cruspo-checkbox-container">
                            <input type="checkbox" value="fri" onChange={(e)=>{this.onChangeDays(e)}}/>
                            <span className="cruspo-checkmark">FRI</span>
                        </label>
                        <label className="cruspo-checkbox-container">
                            <input type="checkbox" value="sat" onChange={(e)=>{this.onChangeDays(e)}}/>
                            <span className="cruspo-checkmark">SAT</span>
                        </label>
                        </div>:<div></div>
                        }
                </div>
                <div class="program-inline-wrapper flex space-between mt-20">
                    <div>
                        <p className="title1">Notify your members via email</p>
                        <p className="description1">This will be send a reminder of your live session to your members.</p>
                    </div>
                    <label className="cruspo-switch">
                        <input type="checkbox" name="isNotify" onChange={(e)=>{this.setState({isNotify:e.target.checked})}}/>
                        <span className="slider"></span>
                    </label>
                </div>
                <div class="program-inline-wrapper flex space-between mt-20">
                    <div>
                        <p className="title1">Limited seats in this batch time</p>
                        <p className="description1">This will limit the number of seats in this particular batch time</p>
                    </div>
                    <label className="cruspo-switch">
                        <input type="checkbox" name="isNotify" onChange={(e)=>{this.setState({limitSeat:e.target.checked})}}/>
                        <span className="slider"></span>
                    </label>
                </div>
                {
                    this.state.limitSeat?
                    <input className="cruspo-input" placeholder="no. of seats"/>:<div></div>
                }
                <button className="cruspo-button mt-10" style={{float:'right',backgroundColor:'#1462e1',color:'white'}} type="submit">Create batch timing</button>
                </form>
            </div>
            </ReactModal>
        );
    }
}

export default AddEditBatchTimingModal;