import React, { Component } from 'react'
import ProgramCard from '../Programs/program-card';
import Gotopagedetails from '../Website/gotopage-details';
import '../../../Assets/css/admin/dashboard.css';
import { getPrograms } from '../../../services/programServices';

export default class Dashboard extends Component {
    
    constructor(props){
        super(props);
        this.state={
            programs:[]
        }
        this.fetchPrograms();
    }

    async fetchPrograms(){
        var programs = await getPrograms();
        if(programs){
            this.setState({programs:programs});
        } 
    }

    render() {
        return (
            <div className="dashboard-wrapper">
                <img src="./Images/dashboard-default-cover-image-p-800.png" className="cruspo-dashboard-image" alt="cruspo-dashboard-background"/>
                <div className="gotopage-overview">
                    <h1>Go to page overview</h1>
                    <Gotopagedetails/>
                </div>
                <div className="programs-overview">
                    <h1>Programs overview</h1>
                    <div className="programs-flex">
                        {
                            this.state.programs.map(p=>{
                                return <ProgramCard details={p}/>
                            })
                        }
                    </div>
                    
                </div>
                <div className="overview-grid">
                    <div className="session-grid-overview">

                    </div>
                    <div className="appintment-grid-overview">

                    </div>
                    <div className="member-overview">

                    </div>
                    <div className="earning-overview">

                    </div>
                    <div className="billing-overview">

                    </div>
                </div>
            </div>
        )
    }
}
