import React, { Component, createRef } from 'react';
import ProgramCard from './program-card';
import '../../../Assets/css/admin/program.css';
import ReactModal from 'react-modal';
import currencies from '../../../helper/currencies';
import { createProgram, getPrograms, getSubCategories } from '../../../services/programServices';
import { uploadFileandGetLink } from '../../../services/resourceServices';
import Loader from 'react-loader-spinner';

export default class Programs extends Component {

    constructor(props){
        super(props);
        this.state={
            isModalOpen:false,
            isPaid:false,
            isLoading:true,
            programs:[],
            categories:[],
            subCategories:[],
            isSubCategoriesLoading:false,
            error:null
            }
        this.thumbnailFiLeRef = createRef(null);
        this.handlesubmit.bind();
    }

    async componentDidMount(){
       var programs = await  getPrograms();
       if(programs)
       {       
            this.setState({
                isLoading:false,
                programs:programs
            });
        }else{

        }
    }

    closeModal = ()=>{
        this.setState({isModalOpen:false})
    }

    async handlesubmit(e){
        e.preventDefault();
        const data = {
            programTitle:this.programTitle,
            programDescription:this.programDescription,
            programThumbnail:await uploadFileandGetLink(this.state.image),
            programType:this.programType,
            isPaid:this.state.isPaid,
            currency:this.currency,
            programPrice:this.programPrice,
            programExpiryDate:this.state.programExpiryDate,
            collectionPeriod:this.state.collectionPeriod,
            programCategory:this.state.programCategory,
            programSubCategory:this.state.programSubCategory
        };

        if(this.validateProgram(data)){
            var res = await createProgram(data);
            if(res){
                this.setState({isModalOpen:false});
            }
        }
    }

    validateProgram(data){
        if(!data.programTitle){
            this.setState({error:'Program title is required'});
            return false;
        }if(!data.programCategory){
            this.setState({error:'Program Category is required'});
            return false;
        }if(!data.programSubCategory){
            this.setState({error:'Program Sub Category is required'});
            return false;
        }
        return true;
    }

    onThumbnailChange = (e)=>{
        this.setState({
            image:e.currentTarget.files[0]
        });
    }

    onProgramCategoryChange = async (e)=>{
        this.setState({programCategory:e.currentTarget.value});
        this.setState({isSubCategoriesLoading:true});
        var categoryId = this.state.categories.find(c=>c.categoryName===e.currentTarget.value)?._id;
        if(categoryId){
            var subCategories = await getSubCategories(categoryId);
            this.setState({subCategories:subCategories});
        }
        this.setState({isSubCategoriesLoading:false});
    }

    render() {
        return (
        <>
            <ReactModal 
                isOpen={this.state.isModalOpen}
                onRequestClose={this.closeModal}
                shouldCloseOnEsc={true}
                shouldCloseOnOverlayClick={true}
                overlayClassName="modal-overlay"
                className="create-program-modal"
            >
                <div className="create-program-modal-header">
                    <p></p>
                    <button class="modal-close-button" onClick={this.closeModal}>Close <img src="/Images/cancel-button.svg" alt="close-button"/></button>
                </div>
                <div className="create-program-modal-content">
                    <div className="flex space-evenly">
                        <div className="program-add-image-wrapper" onClick={(e)=>this.thumbnailFiLeRef.current.click()}>
                            <input type="file" style={{display:'none'}} accept="image/*" ref={this.thumbnailFiLeRef} onChange={this.onThumbnailChange}/>
                            {
                            !this.state.image?
                                <div className="flex" style={{flexDirection:'column',alignItems:'center'}}>
                                    <img src="/Images/upload-thumbnail.svg" alt="thumbnail"/>
                                    <p>Upload your Image</p>
                                </div>:
                            <img src={URL.createObjectURL(this.state.image)} alt="ImageUrl" style={{width:'300px',height:'200px'}}  />
                            }
                        </div>
                        <img src="/Images/cruspo-extra.svg" alt="container"/>
                    </div>
                    <form onSubmit={(e)=>this.handlesubmit(e)} className="create-program-form">
                        <input type="text" placeholder="Give a catchy title to your program" className="cruspo-input" onChange={(e)=>{this.programTitle=e.currentTarget.value}}/>
                        <textarea placeholder="Describe your program" className="cruspo-textarea" onChange={(e)=>{this.programDescription=e.currentTarget.value}}></textarea>
                        <select className="cruspo-select" onChange={(e)=>{this.programType=e.currentTarget.value}} style={{width:'100%'}}>
                            <option>Select Program type</option>
                            <option value="live-program">Live Program</option>
                            <option value="pre-recorded-program">Pre Recorded Program</option>
                        </select>
                        <div className="flex space-between mt-20">
                            <p className="title1">Select a program category</p>
                            <div className="flex">
                            <input type="text" list="categories" placeholder="program category" className="cruspo-input" onChange={(e)=>this.onProgramCategoryChange(e)} />
                            <datalist id="categories">
                                {
                                this.state.categories.map(c=>{
                                    return <option value={c.categoryName}/>
                                })
                                }
                            </datalist>
                            </div>
                        </div>
                        {
                            this.state.programCategory?
                            <div className="flex space-between mt-20">
                                <p className="title1">Select a program sub-category</p>
                                {
                                    this.state.isSubCategoriesLoading?
                                    <Loader
                                        type="TailSpin"
                                        color="#ff9f1c"
                                        height="50px"
                                        width="50px"
                                        />
                                    :<div className="flex">
                                        <input className="cruspo-input" placeholder="program sub category" list="subcategory" disabled={this.state.programCategory?false:true} onChange={(e)=>{this.setState({programSubCategory:e.currentTarget.value})}}/>
                                        <datalist id="subcategory">
                                        {
                                            this.state.subCategories.map(c=>{
                                                return <option value={c.categoryName}/>
                                            })
                                        }
                                        </datalist>
                                    </div>    
                                // </div>
                                }
                            </div>:<div></div>
                        }
                        <div className="cruspo-modal-inline-wrapper space-between">
                            <div>
                                <p className="title1" >Make this a paid program</p>
                                <p className="description1">This will require your audience to pay for your program</p>
                            </div>
                            <label className="cruspo-switch">
                                <input type="checkbox" onChange={(e)=>{this.setState({isPaid:e.target.checked})}}/>
                                <span className="slider"></span>
                            </label>
                        </div>
                        {
                            this.state.isPaid?
                            <>
                                <div className="cruspo-modal-inline-wrapper space-between">
                                    <p className="title1">Give a price to your program</p>
                                    <div className="flex">
                                        <select className="cruspo-select mr-20" onChange={(e)=>{this.currency = e.currentTarget.value}}>
                                        <option>Select Currency</option>
                                        {
                                            currencies.map(c=>{
                                                return <option value={c._id.$oid}>{`${c.fullName} (${c.symbol})`}</option>
                                            })
                                        }
                                        </select>
                                        <input type="number" placeholder="Enter Amount" className="cruspo-input" onChange={(e)=>this.programPrice = e.currentTarget.value}/>
                                    </div>
                                </div>
                        <div className="cruspo-modal-inline-wrapper space-between">
                            <div>
                                <p className="title1">Period of program price collection</p>
                                <p className="description1">In how many intervals or periods you want to collect price.</p>
                            </div>
                            <div className="flex">
                                <label class="cruspo-radio-container">Once
                                    <input type="radio"  name="collectionPeriod" value="ONCE" onChange={(e)=>{
                                        this.setState({collectionPeriod:e.currentTarget.value})
                                        }}/>
                                    <span class="checkmark"></span>
                                </label>
                                <label class="cruspo-radio-container">Monthly
                                    <input type="radio"  name="collectionPeriod" value="MONTHLY" onChange={(e)=>{
                                        this.setState({collectionPeriod:e.currentTarget.value})
                                        }}/>
                                    <span class="checkmark"></span>
                                </label>
                                <label class="cruspo-radio-container">Yearly
                                    <input type="radio" name="collectionPeriod" value="YEARLY" onChange={(e)=>{
                                        this.setState({collectionPeriod:e.currentTarget.value})
                                        }}/>
                                    <span class="checkmark"></span>
                                </label>
                                <label class="cruspo-radio-container">custom
                                    <input type="radio" name="collectionPeriod" value="CUSTOM" onChange={(e)=>{
                                        this.setState({collectionPeriod:e.currentTarget.value,selectedDays:[]})
                                        }}/>
                                    <span class="checkmark"></span>
                                </label>
                            </div>
                        </div>
                        </>:<div></div>
                        }
                        <div>
                            <h4>{this.state.error}</h4>
                        </div>
                        <button className="cruspo-button add-program" type="submit" >Add Program Content</button>
                    </form>
                    
                </div>
            </ReactModal>
            {
            this.state.isLoading?
            <div className="loading-center">
                <Loader
                type="TailSpin"
                color="#ff9f1c"
                height="50px"
                width="50px"
                />
            </div>
            :    
            this.state.programs.length===0?
                <div className="no-programs">
                        <p>You haven't created any course so far. Create your first course by clicking the button below</p>
                        <button className="cruspo-button mt-20" onClick={()=>this.setState({isModalOpen:true})} style={{backgroundColor:'#ff9f1c',padding:'10px 20px'}}>
                            <img src="/Images/add-member.svg" style={{width:'25px',height:'25px',marginBottom:'0px'}} alt="add-program"/>
                            <p className="title1" style={{color:'white'}}>Create You first program</p>
                        </button>
                </div>:
            <div className="program-container">
                {
                    this.state.programs.map(p=>{
                        return <ProgramCard details={p}/>
                    })
                }
                <button className="cruspo-hover-bottom-right-button space-evenly" onClick={()=>this.setState({isModalOpen:true})}>
                    <p className="title1">Create Program</p>
                    <img src="/Images/add-member.svg" style={{width:'25px',height:'25px'}} alt="add-program"/>
                </button>
            </div>
         }
        </>
        )
    }
}
