import React, { Component } from 'react'
import '../../../Assets/css/user/account.css';
import countryCodes from '../../../helper/CountryCodes';
export default class Account extends Component {
    
    updateUser = (e)=>{
        e.preventDefault();
    }

    render() {
        return (
            <div className="account-container">
                <div>
                    <h1 className="">Account settings</h1>
                    <form onSubmit={(e)=>{this.updateUser(e)}}>
                        <div className="flex">
                            <input type="text" className="cruspo-input mr-20" name="firstName" placeholder="First Name" style={{width:'30%'}}/>
                            <input type="text" className="cruspo-input " lastName="lastName" placeholder="Last Name" style={{width:'30%'}}/>
                        </div>
                        <div className="flex mt-20">
                            <select className="cruspo-select mr-20">
                            <option>select country code</option>
                                {
                                    countryCodes.map(c=>{
                                        if(c.dial_code==='+91')
                                        {
                                            return <option value={c.name} selected>{`${c.dial_code}`}</option>
                                        }
                                        return <option value={c.name}>{`${c.dial_code}`}</option>
                                    })
                                }
                            </select>
                            <input type="number" name="email" className="cruspo-input" placeholder="Mobile Number" style={{width:'30%'}}/>
                        </div>
                        <input className="cruspo-input mt-20" placeholder="Email" style={{width:'50%'}}/>
                        <button type="submit" className="update-user-button">Update user</button>
                    </form>
                </div>
            </div>
        )
    }
}
