export async function createBatchTiming(data){
    console.log(data);
    const response  = cruspoFetch(baseURL.adminBaseURL+routes.createBatchTiming,{
        mode:'cors',
        headers:header,
        method:'POST',
        body:JSON.stringify(data)
    });
    if(response.ok){
        store.addNotification({
            title: "Batch timing added successfully",
            message: "batch time added",
            type: "success",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return true;
    }else{
        store.addNotification({
            title: "Error while adding batch time",
            message: "Batch time not created",
            type: "danger",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        })
    }
}