import { store } from "react-notifications-component";
import { baseURL, header, routes, cruspoFetch} from "./config";

export async function createAppointment(data){
    console.log(data);
    var response  = await cruspoFetch(baseURL.adminBaseURL+routes.createAppointment,{
        headers:header,
        body:JSON.stringify(data),
        mode:"cors",
        method:"POST"
    });
    if(response.ok)
    {
        store.addNotification({
            title: "Appointment created Successfully",
            message: "Appointment created",
            type: "success",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return true;
    }else{
        store.addNotification({
            title: "Error while creating Appointment",
            message: "Appointment created",
            type: "danger",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return true;
    }
}

export async function updateAppointment(data,type){
        data.mode=type;
        console.log(data);
        var response = await cruspoFetch(baseURL.adminBaseURL+routes.updateAppointment,{
            body:JSON.stringify(data),
            headers:header,
            mode:'cors',
            method:'POST'
        });
        if(response.ok){
            store.addNotification({
                title: "Appointment updated successfully ",
                message: "Appointment updated",
                type: "success",
                insert: "bottom",
                container: "bottom-right",
                animationIn: ["animate__animated", "animate__fadeIn"],
                animationOut: ["animate__animated", "animate__fadeOut"],
                dismiss: {
                    duration: 5000,
                }
            });
        }else{
            store.addNotification({
                title: "Error while updating Appointment",
                message: "appointment not updated",
                type: "danger",
                insert: "bottom",
                container: "bottom-right",
                animationIn: ["animate__animated", "animate__fadeIn"],
                animationOut: ["animate__animated", "animate__fadeOut"],
                dismiss: {
                    duration: 5000,
                }
            });
        }
}

export async function getBookings(type){
    var response = await cruspoFetch(baseURL.adminBaseURL+routes.getBookings(type),{
        mode:'cors',
        headers:header,
        method:'GET'
    });
    return response.json();
}

export async function getAppointmentById(appointmentId){
    var response = await cruspoFetch(baseURL.adminBaseURL+routes.getAppointmentById(appointmentId),{
        mode:'cors',
        method:'GET',
        headers:header
    });
    if(response.ok){
        return response;
    }else{
        return null;
    }
}
export async function getAppointments(){
    const response = await cruspoFetch(baseURL.adminBaseURL+routes.getAppointments,{
        mode:'cors',
        headers:header,
        method:'GET'
    });
    if(response.ok){
        return await response.json()
    }else{
        return [];
    }
}