import { baseURL, header, routes, cruspoFetch} from "./config";
import {store} from 'react-notifications-component';

export async function userLogin(data){
    if(window.location.hostname==='localhost'){
        data.domainName = 'keshavsuman.cruspo.com'
    }
    const response = await cruspoFetch(baseURL.learnerBaseUrl+routes.userLogin,{
        body:JSON.stringify(data),
        headers:{
            'Content-type':'application/json'
        },
        mode:'cors',
        method:'POST'
    });
    if(response.ok){
        var res = await response.json();
        if(res.message==='PASSWORD_INCORRECT'){
            store.addNotification({
                title: "Password incorrect",
                message: "please type correct password",
                type: "warning",
                insert: "bottom",
                container: "bottom-right",
                animationIn: ["animate__animated", "animate__fadeIn"],
                animationOut: ["animate__animated", "animate__fadeOut"],
                dismiss: {
                    duration: 5000,
                }
            });
            return false;    
        }else if(res.message==='LOGIN_SUCCESSFULL'){
            store.addNotification({
                title: "User login successfully",
                message: "User logged in",
                type: "success",
                insert: "bottom",
                container: "bottom-right",
                animationIn: ["animate__animated", "animate__fadeIn"],
                animationOut: ["animate__animated", "animate__fadeOut"],
                dismiss: {
                    duration: 5000,
                }
            });
            return true;
        } 
    }else{
        store.addNotification({
            title: "Failed to login User",
            message: "can't Log-in user",
            type: "danger",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return false;
    }
}

export async function userSignup(data){
    if(window.location.hostname==='localhost'){
        data.domainName = 'keshavsuman.cruspo.com'
    }
    const response = await cruspoFetch(baseURL.learnerBaseUrl+routes.userSignup,{
        body:JSON.stringify(data),
        mode:'cors',
        method:'POST',
        headers:{
            'Content-type':'application/json'
        }
    });
    if(response.ok){
        store.addNotification({
            title: "User Sign-up successfully",
            message: "User Sign-up successfully",
            type: "success",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return true;
    }else{
        store.addNotification({
            title: (await response.json()).message,
            message: "can't sign-up user",
            type: "danger",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return false;
    }
}