import './App.css';
import { Component } from 'react';
import { BrowserRouter as Router,Switch,Route,Redirect} from 'react-router-dom';

import SideBar from './Component/Admin/SideBar/sidebar';
import CruspoContainer from './Component/Admin/cruspo-admin-container';
import CruspoUserContainer from './Component/User/cruspo-user-container';
import UserSidebar from './Component/User/sidebar/sidebar';
import GoToPage from './Component/Go-to-page';
import UserLogin from './Component/User/Login/login';
import AdminLogin from './Component/Admin/Login/login';
import BookAppointment from './Component/Admin/Appointment/bookAppointment';
import ReactNotification from 'react-notifications-component';
import 'react-notifications-component/dist/theme.css';

class App extends Component{
  
  render(){
    return (
      <Router>
        <ReactNotification />
        <Switch>
          <Route path="/login" component={UserLogin}/>
          <Route path="/admin/login" component={AdminLogin}/>
          <Route path="/bookappointment" component={BookAppointment}/>
          <Route path="/go-to-page" component={GoToPage}/>
          {
            localStorage.getItem('admin_access_key')?
            <>
              <Route path="/admin" render={(props) => <div className="wrapper">
                    <SideBar/>
                    <CruspoContainer/>
                  </div>} />
              <Route path="/user" render={(props)=>
                    <div className="flex"> 
                        <UserSidebar/>
                        <CruspoUserContainer/>
                    </div>
                  } >
              </Route>
              </>
            :
            <Redirect
              to='/login' 
            />}
        </Switch>
      </Router>
    );
  }
}

export default App;
