import React, { Component } from 'react';
import {Link} from 'react-router-dom';

class ProgramCard extends Component {
    
    render() {
        return (
            <div className="program-card">
                <img src={this.props.details.programThumbnail??'/Images/bookappointment.jpeg'} alt="program_thumbnail"/>
                <div className="program-content">
                    <p className="appointment-title">{this.props.details.programTitle}</p>
                    <div className="appointment-status-wrapper">
                        <div className="appointment-status">
                            <div className="published">Published</div>
                            <div className=""></div>
                        </div>
                    </div>
                    <div style={{display:'flex',justifyContent:'space-between',margin:'10px'}}>
                        <div className={{flexDirection:'column'}}>
                            <p>Duration</p>
                            <p>12:30 P.M.</p>
                        </div>
                        <div className={{flexDirection:'column'}}>
                            <p>Last Edited</p>
                            <p>12-Aug-2021</p>
                        </div>
                    </div> 
                    <div className="appointment-card-userprofile">
                        <img src={JSON.parse(localStorage.getItem('user')).profileImage} alt="user_profile"/>
                        <p>{`${this.props.details.feeDetails.currency.symbol} ${this.props.details.feeDetails.price}`}</p>
                    </div>
                    <div style={{display:'flex',justifyContent:'space-between'}}>
                        <button className="appointment-button" type="button">
                                <Link to={`/admin/programs/edit/live/${this.props.details._id}`} >Edit Program</Link>
                                <img src="/Images/arrow-cruspo.svg" alt="arrow_next"/>
                        </button>
                        <button className="appointment-button" type="button" >
                                <p>share program</p>
                                <img src="/Images/share-button.svg" alt="share_logo"/>
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}

export default ProgramCard;
