import React, { Component,useEffect,useState } from 'react';
import Tabs from '../Helpers/Tab';
import '../../../Assets/css/admin/golive.css';
import ReactModal from 'react-modal';
import timezone from '../../../helper/timezone';
import { useRef } from 'react';
import currencies from '../../../helper/currencies';
import moment from 'moment';
import * as goliveServices from '../../../services/goliveServices';
import * as manageMemberServices from '../../../services/managememberServices';
import timeZone from '../../../helper/timezone';
import { uploadFileandGetLink } from '../../../services/resourceServices';
import Loader from 'react-loader-spinner';

export default class Golive extends Component {
    constructor(props){
        super(props)
        this.state = {
            isOpenModalScheduleSession:false,
            isOpenModalScheduleEvent:false
        }
    }
    
    openModalScheduleSession = ()=>{
        this.setState({isOpenModalScheduleSession:true});
    }

    closeModalScheduleSession = ()=>{
        this.setState({isOpenModalScheduleSession:false});
    }
       
    openModalScheduleEvent = ()=>{
        this.setState({isOpenModalScheduleEvent:true});
    }

    closeModalScheduleEvent = ()=>{
        this.setState({isOpenModalScheduleEvent:false});
    }

    render() {
        return (
            <div className="live-wrapper">
                        <ScheduleSessionModal
                            isOpenModalScheduleSession={this.state.isOpenModalScheduleSession}
                            closeModalScheduleSession={this.closeModalScheduleSession}
                        />
                        <ScheduleEventModal
                            isOpenModalScheduleEvent={this.state.isOpenModalScheduleEvent}
                            closeModalScheduleEvent={this.closeModalScheduleEvent}
                        />
                <Tabs>
                    <div label="Go live">
                        <div className="golive-wrapper">
                            <img src="/Images/video-call.svg" alt="video-call"/>
                            <div className="golive-button-wrapper" >
                                <button className="golive-button" >
                                    <p>Go live Now</p>
                                    <img src="/Images/go-live.svg" alt="golive-icon"/>
                                </button>
                                <button className="golive-button" onClick={this.openModalScheduleSession}>
                                    <p>Schedule Session</p>
                                    <img src="/Images/calendar-icon.svg" alt="golive-icon"/>
                                </button>
                                <button className="golive-button" onClick={this.openModalScheduleEvent}>
                                    <p>Schedule live event</p>
                                    <img src="/Images/live-event.svg" alt="golive-icon"/>
                                </button>
                            </div>
                        </div>
                    </div>
                    <div label="Upcoming live sessions">
                        <SessionDetails type="upcoming" onclickcallback={this.openModalScheduleSession}/>
                    </div>
                    <div label="Past live sessions">
                        <SessionDetails type="past" onclickcallback={this.openModalScheduleSession}/>
                    </div>
                </Tabs>
            </div>
        )
    }
}


const NoUpcomingContainer = (props)=>{
    return (
        <div className="no-upcoming-session-wrapper" onClick={props.onclickcallback}>
            <p>{props.title}</p>
            <button type="button" className="flex space-between" style={{alignItems:'center'}}>
                {props.buttonTitle}
                <img src={props.iconURL} alt="icon_alt" style={{width:'30px',marginLeft:'10px'}} onClick={()=>props.onclickcallback()}/>
            </button>
        </div>
    );
}

const SessionDetails = (props)=>{
    const [upcomingSessions, setUpcomingSessions] = useState([]);
    const [isLoading, setisLoading] = useState(true);

    async function fetchSessions(){
        const sessions = await goliveServices.getSessions(props.type);
        setisLoading(false);
        setUpcomingSessions(sessions);
    }
    useEffect(() => {
        fetchSessions();
    },[]);
    
    return (
        <div>
            {
                isLoading?<div className="loading-center">
                <Loader
                type="TailSpin"
                color="#ff9f1c"
                height="50px"
                width="50px"
                />
            </div>:
                upcomingSessions.length===0?<NoUpcomingContainer
                title="There's no upcoming session. Schedule your live session by clicking the button below"
                buttonTitle="Schedule Session"
                iconURL="/Images/calendar-icon.svg"
                onclickcallback={props.onclickcallback}
               />:
                   upcomingSessions.map(session=>{
                       return <ScheduleBar
                            session={session}
                        />
                   })
            }
        </div>
    );
}

const ScheduleBar = (props)=>{
    const [isExpanded,expand]  = useState(false);

    return (
        <div className="schedulebar-wrapper">
            <label> Session</label>
            <div className="schedulebar">
                <img src="/Images/live-event.svg" alt="live_event_icon"/>
                <p>{props.session.sessionTitle}</p>
                <p>{props.session.startTime}</p>
                <p>{`${props.session.duration.hours} hours ${props.session.duration.minutes} mins`}</p>
                <img src="/Images/add-member.svg" alt="add_member_icon" onClick={()=>expand(!isExpanded)}/>
            </div>
            {isExpanded?
                <div className="schedulebar-expanded">
                    <p className="title1">Session Title</p>
                    <p className="description1">{props.session.sessionTitle}</p>
                    <p className="title1">Session Description</p>
                    <p className="description1">{props.session.sessionDescription}</p>
                    <p className="title1">time</p>
                    <p className="description1">{props.session.startTime}</p>
                    <p className="title1">duration</p>
                    <p className="description1">{`${props.session.duration.hours} hours ${props.session.duration.minutes} mins`}</p>
                    <p className="title1">repeat</p>
                    <p className="description1">{props.session.repeatFrequency}</p>
                    <p className="title1">notify members</p>
                    <p className="description1">{props.session.notifyViaEmail?'yes':'no'}</p>

                </div>
            :<></>
            }
        </div>
    );
}


