const ScheduleSessionModal = (props)=>{
    
    const [groups, setgroups] = useState([]);
    
    const [sessionDuration, setSessionDuration] = useState({
        hours:0,
        mins:0
    });
    const [sessionTitle, setSessionTitle] = useState('');
    const [sessionDescription, setSessionDescription] = useState('');
    const [sessionDate, setSessionDate] = useState(null);
    const [sessionTime, setSessionTime] = useState(null);
    const [sessionTimezone, setSessionTimezone] = useState(null);
    const [selectedDays, setSelectedDays] = useState([]);
    const [notifyMemberViaEmail, setNotifyMemberViaEmail] = useState(false);
    const [scheduleSessionForSelectedGroup, setScheduleSessionForSelectedGroup] = useState(false);
    const [repetitionFrequency, setRepetitionFrequency] = useState('ONCE');
    
    const getGroups = async ()=>{
        const groups = await manageMemberServices.getGroups();
        setgroups(groups);
    }

    useEffect(() => {
        getGroups();
    }, []);

    const handleSubmit = async (e)=>{
        e.preventDefault();
        const ok = await goliveServices.createSession({
            sessionTitle:sessionTitle,
            sessionDescription:sessionDescription,
            startDate:sessionDate,
            startTime:sessionTime,
            timeZone:sessionTimezone,
            duration:{
                hours:sessionDuration.hours,
                minutes:sessionDuration.mins
            },
            repeatFrequency:repetitionFrequency,
            notifyViaEmail:notifyMemberViaEmail,
            selectedDays:selectedDays
        });
        if(ok){
            props.closeModalScheduleSession();
        }else{

        }
    }
    
    const onChangeDays = (e)=>{
        var sd = selectedDays;
        if(!sd.includes(e.target.value) && e.target.checked)
        {
            sd.push(e.target.value);
            setSelectedDays(sd);
        }else{
            sd.splice(sd.indexOf(e.target.value), 1);
            setSelectedDays(sd);
        }
    }

    return (
        <ReactModal 
            isOpen={props.isOpenModalScheduleSession}
            shouldCloseOnOverlayClick={true}
            shouldCloseOnEsc={true}
            onRequestClose={props.closeModalScheduleSession}
            className="golive-schedulesession-modal"
            overlayClassName="modal-overlay"
            >
                                <div className="modal-close-row">
                                    <p>Schedule Live Session in 10 Seconds</p>
                                    <button class="modal-close-button" onClick={props.closeModalScheduleSession}>Close <img src="/Images/cancel-button.svg" alt="cancel-button"/></button>
                                </div>
                                <div className="modal-content">
                                    <form onSubmit={(e)=>handleSubmit(e)} className="schedule-live-session-form">
                                        <input type="text" placeholder="Give a title to your session" onChange={(e)=> setSessionTitle(e.currentTarget.value)}/>
                                        <textarea placeholder="Describe your session" onChange={(e)=>setSessionDescription(e.currentTarget.value)}></textarea>
                                        <div class="schedule-inline-wrapper flex" style={{justifyContent:'space-evenly'}}>
                                            <input type="date" min={moment().format('YYYY-MM-DD')} style={{flexBasis:'40%',marginRight:'5%'}} onChange={(e)=>setSessionDate(e.currentTarget.value)}/>
                                            <input type="time" style={{flexBasis:'30%',marginRight:'5%'}} onChange={(e)=>setSessionTime(e.currentTarget.value)}/>
                                            <select className="cruspo-select" style={{width:'50%',margin:'10px 0'}} onChange={(e)=>{setSessionTimezone(e.currentTarget.value)}}>
                                                <option>Select timezone</option>
                                                {
                                                    timezone.map(t=>{
                                                        return <option value={t}>{t}</option>
                                                    })
                                                }
                                            </select>
                                        </div>
                                        <div class="schedule-inline-wrapper flex space-between" style={{alignItems:'center'}} >
                                            <p className="title1" style={{flexBasis:'60%'}}>Duration of the session</p>
                                            <div style={{flexBasis:'50%',justifyContent:'space-evenly'}} className="flex" >
                                                <input type="number" placeholder="hours" style={{width:'20%',marginRight:'20px',flexBasis:'50%'}} onChange={(e)=>{setSessionDuration((prevData)=>{return {hours:e.target.value,mins:prevData.mins}})}}/>
                                                <input type="number" placeholder="mins" style={{width:'20%',flexBasis:'50%'}} onChange={(e)=>{setSessionDuration((prevData)=>{
                                                    return {hours:prevData.hours,mins:e.target.value}})}}/>
                                            </div>
                                        </div>
                                        <div class="schedule-inline-wrapper " style={{margin:'30px 0px'}}>
                                            <div className="flex space-between">
                                                <p className="title1">Repeat your live session </p>
                                                <div className="flex">
                                                    <label class="cruspo-radio-container">Once
                                                        <input type="radio"  name="repeatFrequency" value="ONCE" checked={repetitionFrequency==='ONCE'?true:false} onChange={(e)=>setRepetitionFrequency(e.currentTarget.value)}/>
                                                        <span class="checkmark"></span>
                                                    </label>
                                                    <label class="cruspo-radio-container">Daily
                                                        <input type="radio"  name="repeatFrequency" value="DAILY" onChange={(e)=>setRepetitionFrequency(e.currentTarget.value)}/>
                                                        <span class="checkmark"></span>
                                                    </label>
                                                    <label class="cruspo-radio-container">Mon-Fri
                                                        <input type="radio" name="repeatFrequency" value="MONTOFRI" onChange={(e)=>setRepetitionFrequency(e.currentTarget.value)}/>
                                                        <span class="checkmark"></span>
                                                    </label>
                                                    <label class="cruspo-radio-container">custom
                                                        <input type="radio" name="repeatFrequency" value="CUSTOM" onChange={(e)=>setRepetitionFrequency(e.currentTarget.value)}/>
                                                        <span class="checkmark"></span>
                                                    </label>
                                                </div>
                                            </div>
                                            {
                                            repetitionFrequency==='CUSTOM'?
                                            <div className="flex mt-20" style={{width:'100%',justifyContent:'flex-end'}}>
                                            <label className="cruspo-checkbox-container">
                                                <input type="checkbox" value="sun"  onChange={(e)=>{onChangeDays(e)}}/>
                                                <span className="cruspo-checkmark">SUN</span>
                                            </label>
                                            <label className="cruspo-checkbox-container">
                                                <input type="checkbox" value="mon"  onChange={(e)=>{onChangeDays(e)}}/>
                                                <span className="cruspo-checkmark">MON</span>
                                            </label>
                                            <label className="cruspo-checkbox-container">
                                                <input type="checkbox" value="tue"  onChange={(e)=>{onChangeDays(e)}}/>
                                                <span className="cruspo-checkmark">TUE</span>
                                            </label>
                                            <label className="cruspo-checkbox-container">
                                                <input type="checkbox" value="wed"  onChange={(e)=>{onChangeDays(e)}}/>
                                                <span className="cruspo-checkmark">WED</span>
                                            </label>
                                            <label className="cruspo-checkbox-container">
                                                <input type="checkbox" value="thu" onChange={(e)=>{onChangeDays(e)}}/>
                                                <span className="cruspo-checkmark">THU</span>
                                            </label>
                                            <label className="cruspo-checkbox-container">
                                                <input type="checkbox" value="fri" onChange={(e)=>{onChangeDays(e)}}/>
                                                <span className="cruspo-checkmark">FRI</span>
                                            </label>
                                            <label className="cruspo-checkbox-container">
                                                <input type="checkbox" value="sat" onChange={(e)=>{onChangeDays(e)}}/>
                                                <span className="cruspo-checkmark">SAT</span>
                                            </label>
                                            </div>:<div></div>
                                            }
                                        </div>
                                        <div class="schedule-inline-wrapper flex space-between" style={{margin:'20px 0px 0 0 '}}>
                                            <p className="title1" >Schedule session for selected groups</p>
                                            <label className="cruspo-switch">
                                                <input type="checkbox" name="forSelectedGroup" checked={scheduleSessionForSelectedGroup} onChange={(e)=>setScheduleSessionForSelectedGroup(e.currentTarget.checked)}/>
                                                <span className="slider"></span>
                                            </label>
                                        </div>
                                        {
                                            scheduleSessionForSelectedGroup?
                                            <select className="cruspo-select" style={{marginBottom:'10px',width:'100%'}}>
                                                <option>Select Group</option>
                                                {
                                                    groups.map(g=>{
                                                        return <option valeu={g._id}>{g.groupName}</option>
                                                    })
                                                }
                                            </select>
                                            :<div/>
                                        }
                                        <div class="schedule-inline-wrapper flex space-between">
                                            <p className="title1">Notify your members via email</p>
                                            <label className="cruspo-switch">
                                                <input type="checkbox" name="isNotify" checked={notifyMemberViaEmail} onChange={(e)=>setNotifyMemberViaEmail(e.target.checked)}/>
                                                <span className="slider"></span>
                                            </label>
                                        </div>
                                        <button className="cruspo-button schedule-session-button" type="submit">Schedule session</button>
                                    </form>
                                </div>
                        </ReactModal>
    );
}