import { baseURL, header, routes, cruspoFetch } from "./config";
import { store } from "react-notifications-component";

export async function getGroups(){
    const response = await cruspoFetch(baseURL.adminBaseURL+routes.getGroups,{
        mode:'cors',
        headers:header,
        method:'GET'
    });
    if(response.ok){
        return (await response.json()).data;
    }else{
        store.addNotification({
            title: "Can't get Groups",
            message: "Error while getting Groups",
            type: "danger",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return [];
    }
}

export async function createMember(data){
    const response = await cruspoFetch(baseURL.adminBaseURL+routes.addMember,{
        body:JSON.stringify(data),
        mode:'cors',
        headers:header,
        method:'POST'
    });
    return response;
}
