import { useState } from "react";
import ReactModal from "react-modal";
import * as programService from '../../../../services/programServices';

const AddBatchModal = (props)=>{
    const [batchTitleState, setBatchTitleState] = useState('');
    const [batchDescriptionState, setBatchDescriptionState] = useState('');
    
    async function createBatch(e){
      e.preventDefault();
      const res = await programService.createBatch({
        batchTitle:batchTitleState,
        batchDescription:batchDescriptionState
      },
      window.location.href.split('/').pop()
      );
      if(res){
        props.closeAddBatchModal();
      }
    }
    return (
      <ReactModal
        isOpen={props.isOpen}
        shouldCloseOnEsc="true"
        shouldCloseOnOverlayClick="true"
        onRequestClose={props.closeAddBatchModal}
        className="create-batch-modal"
        overlayClassName="modal-overlay"
      >
        <div className="create-batch-modal-header">
        <button class="modal-close-button" onClick={props.closeAddBatchModal}>Close <img src="/Images/cancel-button.svg" alt="cancel-icon"/></button>
        </div>
        <form onSubmit={(e)=>createBatch(e)}>
          <div className="create-batch-modal-body">
              <p className="title1 " style={{fontSize:'35px'}}>Create batch</p>
              <input placeholder="Name of the batch" className="cruspo-input mt-20" onChange={(e)=>setBatchTitleState(e.currentTarget.value)}/>
              <textarea placeholder="Description of the batch" className="cruspo-textarea" onChange={(e)=>setBatchDescriptionState(e.currentTarget.value)}></textarea>
              <button className="cruspo-button create-batch-button" type="submit">Create batch</button>
          </div>
        </form>
      </ReactModal>
    );
  }

  export default AddBatchModal;