import React, { Component } from 'react';
import '../../../Assets/css/admin/settings.css';
import countryCodes from '../../../helper/CountryCodes';
import * as settingServices from  '../../../services/settingsServices';

export default class settings extends Component {
    
    constructor(props)
    {
        super(props);
        this.user = JSON.parse(localStorage.getItem('user'));
        this.state = {
            firstName:this.user.firstName,
            lastName:this.user.lastName,
            email:this.user.email,
            userName:this.user.userName
        }
    }

    handleSubmit = async (e)=>{
        e.preventDefault();
        const response = await settingServices.updateUser({
            firstName:this.state.firstName,
            lastName:this.state.lastName,
            email:this.state.email,
            contactNumber:{
                countryCode:this.state.countryCode,
                mobileNumber:this.state.mobileNumber
            }
        });
        if(response){
            
        }
    }
    
    handleWebiteMigrationSubmit = async (e)=>{
        e.preventDefault();
        const response = await settingServices.websiteMigrationRequest({
            website:this.state.website
        });
    }

    render() {
        return (
            <>
            <div className="setting-wrapper">
                <h1>Account Settings</h1>
                <form className="account-setting-form" onSubmit={(e)=>{this.handleSubmit(e)}}>
                    <div className="first-last-name-wrapper inline">
                        <input type="text" placeholder="First Name" value={this.state.firstName} onChange={(e)=>this.setState({firstName:e.currentTarget.value})}/>
                        <input type="text" placeholder="Last Name" value={this.state.lastName} onChange={(e)=>this.setState({lastName:e.currentTarget.value})}/>
                    </div>
                    <div className="subdomain-wrapper">
                        <input type="text" placeholder="Sub-Domain" value={this.state.userName} readOnly/>
                        <h1>.cruspo.com</h1>
                        <p>* this will be your default cruspo generated domain</p>
                    </div>
                    <input type="text" placeholder="Email" value={this.state.email} onChange={(e)=>this.setState({email:e.currentTarget.value})}/>
                    <div className="flex" style={{margin:'0px 15px'}}>
                         <select className="cruspo-select" style={{maxWidth:'10%',marginLeft:'0px',marginRight:'10px',marginTop:'10px',marginBottom:'10px',padding:'10px'}} onChange={(e)=>{this.setState({countryCode:e.currentTarget.value})}}>
                            <option>select country code</option>
                            {
                                countryCodes.map(c=>{
                                    if(c.dial_code==='+91')
                                    {
                                        return <option value={c.dial_code} selected>{`${c.dial_code}`}</option>
                                    }
                                    return <option value={c.dial_code}>{`${c.dial_code}`}</option>
                                })
                            }
                        </select>
                        <input type="text" placeholder="Mobile Number" value={this.state.mobileNumber} onChange={(e)=>{this.setState({mobileNumber:e.currentTarget.value})}}/>
                    </div>
                    <button type="submit" className="save-changes-button">save changes</button>
                </form>
                <h1>Website Settings</h1>
                <div className="website-settings">
                    <form className="website-settings-form" onSubmit={(e)=>{this.handleWebiteMigrationSubmit(e)}}>
                        <p>Already have a website? Request for migration</p>
                        <input type="text" placeholder="Existing website domain" onChange={(e)=>{this.setState({website:e.currentTarget.value})}}/>
                        <button type="button" className="send-request-button">Send Request</button>
                    </form>
                </div>
            </div>
                
            </>
        )
    }
}
