import { baseURL, header, routes, cruspoFetch } from "./config";
import { store } from "react-notifications-component";
export async function uploadFileandGetLink(file){
    if(!file){
        return null;
    }else{
        var url = await new Promise(async (resolve,reject)=>{
                var response = await cruspoFetch(baseURL.adminBaseURL+routes.getFileUploadLink(file.name),{
                    headers:header,
                    mode:'cors',
                    method:'GET'
                });
                if(response.ok){
                    const uploadURL = (await response.json()).url;
                    var xhr = new XMLHttpRequest();
                    xhr.open('PUT',uploadURL);
                    xhr.upload.onprogress = function(e) {
                        var done = e.position || e.loaded, total = e.totalSize || e.total;
                        console.log(Math.floor(done/total*1000)/10);
                    };
                    xhr.addEventListener('load',async function(){
                        var res = await cruspoFetch(baseURL.adminBaseURL+routes.registerUploadedFile,{
                            headers:header,
                            body:JSON.stringify({
                                fileName:file.name,
                                fileSize:file.size,
                                fileType:file.type,
                                fileUrl:uploadURL.split('?')[0]
                            }),
                            mode:'cors',
                            method:'POST'
                        });
                        if(res.ok){
                            resolve(uploadURL.split('?')[0]);
                            // return ;
                        }else{
                            console.log(res);
                        }
                    });
                    xhr.addEventListener('error',(error)=>{
                        console.log(error);
                        reject(error);
                    })
                    xhr.send(file);
                }else{
                    console.log(response);
                }
            });
            console.log(url);
            return url;
        }
    }
    
    export async function fetchFiles(){
        var response = await cruspoFetch(baseURL.adminBaseURL+routes.getFiles,{
            headers:header,
            mode:'cors',
            method:'GET'
        });
        if(response.ok){
            var data = await response.json();
            return {
                recentFiles:data.files.splice(0,4),
                files:data.files.splice(4)
            };
        }else{

        }
}

export async function createFolder(data){
    const response = await cruspoFetch(baseURL.adminBaseURL+routes.createFolder,{
        body:JSON.stringify(data),
        mode:'cors',
        headers:header,
        method:'POST'
    });
    if(response.ok){
        store.addNotification({
            title: `${data.folderName} folder created`,
            message: "New folder created",
            type: "success",
            insert: "bottom",
            container: "bottom-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
                duration: 5000,
            }
        });
        return true;
    }else{
        return false;
    }
}

export async function fetchFolders(){
    const response = await cruspoFetch(baseURL.adminBaseURL+routes.getFolders,{
        mode:'cors',
        headers:header
    });
    return response;
}