import React, { Component } from 'react';
import '../../../Assets/css/admin/appointment.css';
import { Link } from 'react-router-dom';
import moment from 'moment';

export default class appointmentCard extends Component {
     
    render() {
        return (
            
            <div className="appointment-card">
                <img src={this.props.details.appointmentThumbnail??'/Images/bookappointment.jpeg'} className="appointment-card-img" alt="appointment_thumbnail"/>
                <div className="appointment-content">
                    <p className="appointment-title">{this.props.details.appointmentTitle}</p>
                    <div className="appointment-status-wrapper">
                        <div className="appointment-status">
                            <div className="published">Published</div>
                            <div className="">unpublished</div>
                        </div>
                    </div>
                    <div style={{display:'flex',justifyContent:'space-between',margin:'10px'}}>
                        <div className={{flexDirection:'column'}}>
                            <p className="title1">Duration</p>
                            {
                                this.props.details.duration?
                                <p>{`${this.props.details.duration.hours} hour ${this.props.details.duration.minutes} mins`}</p>:
                                <p>NA</p>
                            }
                        </div>
                        <div className={{flexDirection:'column'}}>
                            <p className="title1">Last Edited</p>
                            <p>{moment(this.props.details.updatedAt).format('YYYY-MM-DD')??'NA'}</p>
                        </div>
                    </div> 
                    <div className="appointment-card-userprofile">
                        <img src={JSON.parse(localStorage.getItem('user')).profileImage} alt="user_profile"/>
                        <p>{`${this.props.details.currency.symbol} ${this.props.details.appointmentPrice}`}</p>

                    </div>
                    <div style={{display:'flex',justifyContent:'space-between'}}>
                        <button className="appointment-button" type="button">
                            {/* <EditAppoiment props={data} /> */}
                                <Link to={`/admin/appointment/edit/${this.props.details._id}`}>Edit Appointment</Link>
                                <img src="/Images/arrow-cruspo.svg" alt="arrow_next"/>
                        </button>
                        {
                            this.props.details.status!=='DRAFT'?
                            <button className="appointment-button" type="button" >
                                    <Link to={`/bookappointment/${this.props.details._id}`}>share appointment</Link>
                                    <img src="/Images/share-button.svg" alt="share_logo"/>
                            </button>:<div></div>
                        }
                    </div>
                </div>
            </div>
        )
    }
}
